* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

div {
  padding: 15px 0;
}

form {
  width: 300px;
  margin: 0 auto;
  position: relative;
}

.wrapper {
  background: #a3d0c3;
}

.wrapper input {
  min-width: 300px;
  height: 42px;
  color: #9e9c9c;
  background: #fffefb;
  border: 2px solid #7ba7ab;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
}

.wrapper button {
  width: 42px;
  height: 42px;
  cursor: pointer;
  background: #7ba7ab;
  border: none;
  border-radius: 0 5px 5px 0;
  position: absolute;
  top: 0;
  right: 0;
}

.wrapper button:hover {
  background: #6f9fa3;
}

.wrapper button:before {
  content: "";
  color: #f9f0da;
  font-size: 16px;
}

